footer {
  background: #383841;
  padding-top: 118px;
  padding-bottom: 80px;
}
footer .primary .logo p {
  margin-top: 35px;
  font-size: 15px;
  font-weight: 400;
  line-height: 26px;
  width: 260px;
  color: #ACB1C0;
}
footer .primary .logo .socials a {
  transition-duration: 0.4s;
  width: 45px;
  height: 45px;
  justify-content: center;
  align-items: center;
  display: inline-flex;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.1);
}
footer .primary .logo .socials a img {
  transition-duration: 0.4s;
  opacity: 0.5;
}
footer .primary .logo .socials a:hover {
  background-color: #423EF3;
}
footer .primary .logo .socials a:hover img {
  opacity: 1;
}
body {
  margin: 0;
  font-family: "DM Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wrapper {
  width: 1290px;
  margin: 0 auto;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

img {
  line-height: 0;
}